// Add your codes here
.csr {
    width: 100vw;
    overflow: hidden;
    padding-bottom: 48px;
    * {
        font-family: "Roboto", sans-serif;
    }

    &--main {
        padding: 48px 0 96px;
    }

    &--sport-1 {
        .csr__company {
            margin-bottom: 80px;
        }
    }

    &--news {
        .news-item {
            padding: 0 !important;
            display: block;
            margin-bottom: 32px;
        }

        .news-widget__action {
            width: 100%;
            margin-top: 100px;
            margin-bottom: 64px;
        }

        .news-item__date {
            margin: 16px 0 !important;
        }

        .csr__heading {
            @media (max-width: 768px) {
                margin-top: 64px;
                text-align: center;
            }
        }
    }

    &--news-1 {
        .csr__heading {
            text-align: left;
            margin-top: 32px;
        }
    }

    &--ecology-2 {
        .csr-icons__item {
            width: 100%;
        }

        @media (max-width: 768px) {
            .csr__heading--small {
                text-align: center;
            }
        }
    }
    &__heading {
        font-style: normal;
        font-weight: normal;
        font-size: 35px;
        line-height: 41px;
        color: #1c4982;
        margin-bottom: 48px;
        text-align: center;

        &--small {
            font-size: 30px;
            line-height: 35px;
        }

        @media (max-width: 768px) {
            font-size: 24px;
            line-height: 28px;
            margin-bottom: 24px;
            text-align: left;
        }
    }

    &__content {
        max-width: 898px;
        margin: 0 auto 48px;
    }

    &__img {
        max-width: 100%;
        display: block;
        margin: 0 auto 48px;

        &--mobile {
            display: none;
        }

        @media (max-width: 480px) {
            &--desktop {
                display: none;
            }

            &--mobile {
                display: block;
            }
        }
    }

    &__text {
        margin-bottom: 64px;
    }

    &__desc {
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 22px;
        color: #000;
        text-align: center;
        max-width: 720px;
        margin: 0 auto;

        span {
            font-size: 22px;
            line-height: 22px;
            color: #172ea5;
        }
    }

    &__icon {
        background: #172ea5;
        display: flex;
        align-items: center;
        justify-content: center;

        &--small {
            width: 70px;
            height: 70px;
            box-shadow: 4px 5px 15px rgba(23, 46, 165, 0.4);
            border-radius: 4px;
        }

        &--big {
            width: 150px;
            height: 150px;
            border-radius: 12px;
            position: relative;

            &::after {
                position: absolute;
                content: "";
                background: #eaecee;
                border-radius: 12px;
                width: 150px;
                height: 150px;
                left: 20px;
                top: -18px;
                z-index: -1;
            }

            img {
                width: 65px;
            }
        }

        &--grey {
            background: #eaecee;
            width: 150px;
            height: 150px;
            border-radius: 12px;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 8px;
            box-shadow: 0 3px 10px rgba(0, 0, 0, 0.25);
            margin: auto;
        }
    }

    &__back {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        color: #433491;
        margin-bottom: 32px;

        @media (max-width: 768px) {
            display: none;
        }
    }

    &__digit {
        font-weight: bold;
        font-size: 85px;
        line-height: 85px;
        color: #172ea5;
        text-align: center;

        span {
            font-size: 40px;
        }

        @media (max-width: 768px) {
            font-size: 55px;
            line-height: 65px;
        }
    }

    &__btn {
        background: #172ea5;
        box-shadow: 0 1px 2px rgba(97, 97, 97, 0.2);
        border-radius: 8px;
        padding: 18px 16px;
        width: fit-content;
        font-size: 15px;
        line-height: 20px;
        color: #fff;

        &:hover {
            color: #fff;
            opacity: 0.8;
        }
    }

    &__company {
        text-align: center;
        margin-bottom: 40px;

        img {
            max-width: 90vw;
            margin: auto;
            transform: scale(0.7);
        }
    }

    &-menu {
        margin-bottom: 80px;
        &__item {
            width: 100%;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 322px;

            &--child {
                align-items: flex-start;
                justify-content: flex-start;
                flex-direction: column;
                overflow: hidden;

                .csr-menu {
                    &__heading {
                        top: 46px;
                        text-align: left;
                        left: 16px;
                    }

                    &__desc {
                        position: absolute;
                        top: 124px;
                        text-align: left;
                        left: 16px;
                        z-index: 2;
                        font-style: normal;
                        font-weight: normal;
                        font-size: 16px;
                        line-height: 20px;
                        color: #fff;

                        @media (max-width: 768px) {
                            top: 88px;
                        }
                    }

                    &__btn {
                        position: absolute;
                        bottom: 32px;
                        left: 16px;
                        z-index: 2;

                        img {
                            height: 45px;
                        }
                    }
                }
            }

            @media (max-width: 768px) {
                height: 230px;
            }

            &::after {
                content: " ";
                position: absolute;
                left: 0;
                right: 0;
                width: 100%;
                height: 100%;
                transition: 0.2s;
                background: linear-gradient(115.4deg, #172ea5 -15.13%, rgba(183, 194, 254, 0) 114.51%);
            }

            &:hover {
                &::after {
                    background: #172ea5;
                    transition: 0.2s;
                }
            }
        }

        &__bg {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &__heading {
            width: 100%;
            text-align: center;
            position: absolute;
            font-style: normal;
            font-weight: bold;
            font-size: 35px;
            line-height: 41px;
            color: #fff;
            z-index: 2;

            @media (max-width: 768px) {
                font-size: 22px;
                line-height: 26px;
            }
        }

        @media (max-width: 768px) {
            margin-bottom: 48px;
        }
    }

    &-icons {
        margin-bottom: 80px;

        &--big {
            margin-bottom: 96px;
            position: relative;
        }

        &__heading {
            max-width: 553px;
            text-align: center;
            margin: 0 auto 48px;

            &--blue {
                font-style: normal;
                font-weight: bold;
                font-size: 30px;
                line-height: 60px;
                color: #202020;
                text-align: left;
                @media (max-width: 768px) {
                    text-align: center;
                }
            }

            @media (max-width: 768px) {
                margin: 0 auto 24px;
            }
        }

        &__list {
            display: flex;
            align-items: center;
            justify-content: center;

            &--first {
                margin-bottom: 80px;

                @media (max-width: 768px) {
                    margin-bottom: 48px;
                }
            }

            @media (max-width: 768px) {
                justify-content: center;
                flex-direction: column;
                margin-bottom: 48px;
            }
        }

        &__item {
            width: 210px;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
            height: 100%;
            margin: 0 15px;

            &--big {
                width: 33%;
                margin: 0 8px;

                @media (max-width: 768px) {
                    width: 100%;
                }
            }
            @media (max-width: 768px) {
                margin-bottom: 40px;
            }
        }

        &__title {
            margin-top: 24px;
            min-height: 40px;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            text-align: center;
            color: #373737;

            &--big {
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
                margin-top: 28px;

                span {
                    color: #172ea5;
                    white-space: nowrap;
                }
            }

            &--middle {
                font-weight: 500;
                font-size: 18px;
                line-height: 20px;
                margin-top: 48px;
                padding: 0 8px;

                span {
                    color: #172ea5;
                    white-space: nowrap;
                }
            }

            @media (max-width: 768px) {
                min-height: auto;
            }
        }

        @media (max-width: 768px) {
            margin-bottom: 64px;

            &--big {
                margin-bottom: 48px;
            }
        }
    }

    &-slider {
        position: relative;

        &__btn {
            position: absolute;
            cursor: pointer;

            @media (max-width: 480px) {
                transform: scale(0.6);
            }
        }
    }

    &-partners {
        margin-bottom: 80px;
        &__item {
            background: #fff;
            box-shadow: 0 1px 10px rgb(0, 0, 0, 0.1);
            padding: 16px 8px;
            width: 220px;
            height: 200px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-left: 12px;
        }

        &__logo {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100px;

            img {
                flex: none;
            }
        }

        &__name {
            text-align: center;
            height: 100%;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: #373737;
        }
        .slick-list {
            overflow: visible;
        }
    }

    &-blue {
        background: #172ea5;
        padding: 46px 16px;
        margin-bottom: 80px;

        &__text {
            font-style: normal;
            font-weight: 500;
            font-size: 25px;
            line-height: 35px;
            text-align: center;
            color: #fff;
            max-width: 895px;
            margin: 0 auto;

            @media (max-width: 768px) {
                font-size: 18px;
                line-height: 25px;
            }
        }

        @media (max-width: 768px) {
            margin-bottom: 48px;
        }
    }

    &-nav {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 70px;

        @media (max-width: 768px) {
            width: 100%;
            overflow-x: scroll;
            justify-content: flex-start;
            height: 50px;
        }

        &__item {
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 22px;
            margin: 0 25px;
            color: rgba(86, 121, 165, 0.8);

            @media (max-width: 768px) {
                width: fit-content;
                white-space: nowrap;
            }

            &.active {
                font-weight: 500;
                color: #345b8a;
            }

            &:hover {
                color: #000;
            }
        }
    }

    &-banner {
        position: relative;
        height: 478px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 80px;

        @media (max-width: 768px) {
            margin-bottom: 48px;
        }

        &__bg {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;

            &--left {
                object-position: left center;
            }
        }

        &::after {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            content: " ";
            background: rgba(64, 64, 64, 0.5);
        }

        &__heading {
            font-style: normal;
            font-weight: bold;
            font-size: 55px;
            line-height: 64px;
            z-index: 2;
            color: #fff;
            max-width: 700px;
            text-align: center;

            @media (max-width: 768px) {
                font-size: 40px;
                line-height: 47px;
                padding: 16px;
            }
        }
    }

    &-grid {
        margin-bottom: 80px;

        @media (max-width: 768px) {
            margin-bottom: 48px;

            .col-md-6 {
                &:nth-child(2) {
                    order: 0;
                }
            }
        }

        .col-md-6 {
            order: 1;
            &:nth-child(2),
            &:nth-child(3) {
                * {
                    background: #172ea5;
                    color: #fff;
                }
            }
        }
        &__item {
            padding: 28px 20px;
            display: flex;
            align-items: flex-start;
            height: 258px;
            flex-direction: column;
            margin-bottom: 24px;
            box-shadow: 0 6px 12px rgba(0, 0, 0, 0.07);
            border-radius: 4px;
            background: #fff;
        }

        &__digit {
            font-weight: bold;
            font-size: 55px;
            line-height: 55px;
            color: #172ea5;
            margin-bottom: 36px;
        }

        &__desc {
            font-weight: 500;
            font-size: 18px;
            line-height: 23px;
            color: #172ea5;
        }
    }

    &__info-grid {
        .row {
            margin-bottom: 48px;
        }

        .col-md-4 {
            align-items: center;
            justify-content: center;
            display: flex;

            img {
                max-width: 100%;
            }

            @media (max-width: 768px) {
                margin-bottom: 24px;
            }
        }

        .col-md-7 {
            display: flex;
            align-items: center;
            .csr__desc {
                text-align: left;
            }
        }
    }

    &-films-company {
        width: 100%;
        max-width: 1092px;
        margin: 0 auto 80px;

        &__item {
            width: 210px;
        }

        &__title {
            font-weight: 500;
            font-size: 18px;
            line-height: 23px;
            text-align: center;
            margin-top: 16px;
        }
    }

    &-films-books {
        width: 100%;
        margin-bottom: 80px;

        .slick-track {
            margin-left: 0;
        }
    }
    &-films-dub {
        width: 100%;
        margin-bottom: 80px;

        &__item {
            position: relative;
            padding: 8px;
            height: 440px;

            &::after {
                position: absolute;
                left: 8px;
                top: 8px;
                content: " ";
                width: calc(100% - 16px);
                height: calc(100% - 16px);
                background: linear-gradient(180deg, rgba(29, 29, 29, 0) 0%, rgba(29, 29, 29, 0.8) 80.79%);
            }
        }

        &__prev {
            top: 200px;
            left: -20px;
            z-index: 3;
        }

        &__next {
            top: 200px;
            right: -20px;
            z-index: 3;
        }

        &__bg {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &__info {
            position: absolute;
            width: calc(100% - 16px);
            height: calc(100% - 16px);
            left: 8px;
            top: 8px;
            z-index: 2;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-end;
            padding: 24px 32px;
        }

        &__category {
            font-size: 14px;
            line-height: 16px;
            color: #0feffd;
            margin-bottom: 22px;
        }

        &__rating {
            width: 80px;
            height: 16px;
            margin-bottom: 16px;
        }

        &__stars {
            background: url(/assets/img/other/stars.svg);
            width: 100%;
            height: 100%;
        }

        &__title {
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;
            color: #fff;
        }
    }

    .m0 {
        margin: 0;
    }

    .p0 {
        padding: 0;
    }

    .mb48 {
        margin-bottom: 48px;
    }

    a {
        &:hover {
            text-decoration: none;
        }
    }

    .news {
        &-widget {
            &__slider {
                margin-bottom: 64px;

                @media (max-width: 480px) {
                    .news-item {
                        max-width: 310px;
                    }
                }
            }

            &__prev {
                top: 140px;
                left: -20px;

                @media (max-width: 480px) {
                    top: 100px;
                }
            }

            &__next {
                top: 140px;
                right: -20px;
                @media (max-width: 480px) {
                    top: 100px;
                }
            }

            &__action {
                display: flex;
                justify-content: center;
            }
        }

        &-item {
            padding: 8px;

            &__banner {
                height: 278px;
                width: 100%;

                @media (max-width: 480px) {
                    height: 220px;
                }

                img {
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }

            &__date {
                font-style: normal;
                font-weight: normal;
                font-size: 13px;
                line-height: 18px;
                color: #7b7b7b;
                display: flex;
                align-items: center;
                margin: 20px 0 22px;

                &::before {
                    content: url(/assets/img/icons/date.svg);
                    margin-right: 8px;
                }
            }

            &__title {
                font-style: normal;
                font-weight: 600;
                font-size: 18px;
                line-height: 23px;
                color: #333;
                margin-bottom: 16px;
            }

            &__desc {
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 22px;
                color: #7b7b7b;
            }

            &__content {
                columns: 300px 2;

                p {
                    margin-bottom: 16px;
                }

                img {
                    width: 100%;
                    display: block;
                    margin-bottom: 16px;
                }
            }
        }
    }

    .gallery-widget {
        margin-bottom: 80px;
        &__slide {
            width: 100%;
            height: 300px;
            position: relative;
            overflow: hidden;
            cursor: pointer;

            @media (max-width: 768px) {
                height: 250px;
            }

            @media (max-width: 480px) {
                max-width: 310px;
                padding: 8px;
            }

            &::after {
                position: absolute;
                content: "";
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                background: rgba(0, 0, 0, 0.5);
                @media (max-width: 480px) {
                    display: none;
                }
            }

            &.slick-current {
                &::after {
                    display: none;
                }
            }

            &:hover {
                img {
                    transform: scale(1.1);
                    @media (max-width: 480px) {
                        transform: scale(1);
                    }
                }
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: 0.3s;
            }

            &--big {
                height: 556px;
                width: 100%;
                overflow: hidden;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
        &__big {
            width: 90%;
            margin-left: 5%;
            margin-bottom: 28px;
            @media (max-width: 768px) {
                display: none;
            }
        }

        &__prev {
            top: 140px;
            left: -20px;
            z-index: 2;

            @media (max-width: 768px) {
                top: 100px;
            }
        }

        &__next {
            top: 140px;
            right: -20px;
            z-index: 2;
            @media (max-width: 768px) {
                top: 100px;
            }
        }
    }
}
